body{font-family:'Sawarabi Mincho', sans-serif;margin:0}.grid{width:100%;box-sizing:border-box;padding:10px;display:grid;grid-template-columns:1fr 1fr;width:100%;background:lightgrey;background:-webkit-gradient(linear, left top, right bottom, from(rgba(55,55,55,0.4)), to(rgba(225,225,225,0.4))),url(/outfitter/static/media/giphy2.42e10ffb.gif) repeat center center;background:linear-gradient(to bottom right, rgba(55,55,55,0.4), rgba(225,225,225,0.4)),url(/outfitter/static/media/giphy2.42e10ffb.gif) repeat center center;background-size:cover}@media screen and (max-width: 992px){.grid{grid-template-columns:1fr}}.col{margin:20px 20px;text-align:center;padding:20px;border-radius:20px;background:white}@media screen and (max-width: 992px){.col{margin:20px 20px 40px}}h1{margin:0}.row-4{width:100%;display:grid;grid-template-columns:1fr 1fr 1fr 1fr;width:100%;margin:10px 0;padding:10px 0;height:100%;max-height:110px}.row-4 div{max-height:100%}.row-4 img{max-width:100%;max-height:80px}.row-4 p{font-size:10px;text-transform:uppercase;font-weight:bold}.navbar{width:100%;text-align:center;padding:20px 0}.navbar img{width:300px}.wishlistWrapper{padding:30px;text-align:center;background:black;color:white}.wishlist{max-height:100%;display:grid;grid-template-columns:repeat(8, 1fr);background:white;height:100%;border-radius:20px;z-index:100}@media screen and (max-width: 992px){.wishlist{grid-template-columns:repeat(3, 1fr)}}.wishlistItem{text-align:center;text-transform:uppercase;font-size:10px;font-weight:bold;padding:20px 0;max-width:100%}.wishlistItem img{max-height:80px}.wishlistItem p{color:black}.blackdrip,.blackdripWrapper{width:100%;margin-bottom:-60px;z-index:-1}@media screen and (max-width: 992px){.blackdrip,.blackdripWrapper{margin-bottom:-10px}}.blackdripWrapper{display:flex}

